<template>
<div class="relative flex flex-col min-w-0 break-words bg-white rounded mb-6 xl:mb-0 border-b-2 border hover:shadow-lg transform transition duration-500 hover:scale-110">
    <div class="flex-auto p-4">
        <div class="flex flex-wrap">
            <div class="relative w-full pr-4 max-w-full flex-grow flex-1">
                <span class="font-semibold text-base md:text-xl text-gray-800">
                    {{ statSubtitle }}
                </span> 

                <h3>
                    <button class="text-blue-500 transform transition duration-500 hover:scale-110" @click="$router.push(statRoute)">ดูรายละเอียด</button>
                </h3>

            </div>
            
            <div class="relative w-auto pl-4 flex-initial">
                <div class="text-white p-3 text-center inline-flex items-center justify-center w-12 h-12 shadow-lg rounded-lg" :class="[statIconColor]">
                    <i :class="[statIconName]"></i>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
    name: "card-stats",
    props: {
        statSubtitle: {
            type: String,
            default: "Traffic",
        },

        statArrow: {
            default: "up",
            validator: function (value) {
                // The value must match one of these strings
                return ["up", "down"].indexOf(value) !== -1;
            },
        },
        statPercent: {
            type: String,
            default: "3.48",
        },
        // can be any of the text color utilities
        // from tailwindcss
        statPercentColor: {
            type: String,
            default: "text-blue-500",
        },
        statTitle: {
            type: String,
            default: "350,897",
        },
        statDescripiron: {
            type: String,
            default: "Since last month",
        },
        statIconName: {
            type: String,
            default: "far fa-chart-bar",
        },
        // can be any of the background color utilities
        // from tailwindcss
        statIconColor: {
            type: String,
            default: "bg-red-500",
        }, 
        statRoute: {
            type: String,
            default: "#"
        }, 
    },
};
</script>
<style>
.shadow-all {
  -webkit-box-shadow: 0px 0px 28px 0px rgba(184,184,184,1);
  -moz-box-shadow: 0px 0px 28px 0px rgba(184,184,184,1);
  box-shadow: 0px 0px 28px 0px rgba(184,184,184,1);
}
